import CatalogoControlerInstance from "./CatalogoControler";
import { ProdutoCarinho } from "./Classes" 
class CarrinhoControler{
    TodosOsItens=[];

    Adicionar=(Produto, Quantidade)=>{
        this.TodosOsItens.push(
            new ProdutoCarinho(
                Produto,
                Quantidade
            )
        )
    }

    Atualizar=(Index, Quantidade)=>{this.TodosOsItens[Index].Quantidade=Quantidade}

    Remover=(Index)=>{this.TodosOsItens.pop(Index,1)}

    CalcularValor = (formaPagamento) => {
        let valorTotal = 0;
        this.TodosOsItens.forEach(item => {
            if(formaPagamento===""){
                return "##,##"
            }else if(formaPagamento==="Dim"||formaPagamento==="Pix"){
                valorTotal += item.Quantidade * item.Produto.Preco.DinPix
            }else{
                valorTotal += item.Quantidade * item.Produto.Preco.Cart
            }
        });
    
        return valorTotal.toFixed(2); // Formata o valor com duas casas decimais
    }
    
    ObterItens=()=>{return this.TodosOsItens}
    
    salvarNoBrowser = () => {
        localStorage.setItem('carrinho',this.TodosOsItens.map((Value)=>{
            return `${Value.Quantidade}:${Value.Produto.Id}/*/`
        }));
    }
      
    obterDoBrowser = () => {
        this.TodosOsItens=[];
        const dadosCarrinho = localStorage.getItem('carrinho');
        if(dadosCarrinho&&dadosCarrinho!==""){
            const Itens=dadosCarrinho.split('/*/')
            Itens.forEach((Value)=>{
                Value=Value.replace(/,/g, '')
                if(Value!==""){
                    this.TodosOsItens.push(
                        new ProdutoCarinho(
                            CatalogoControlerInstance.ObterItemByIndex(Value.split(":")[1]),
                            Value.split(":")[0],
                        )
                    )
                }
            })
        }
    }
}
const CarrinhoControlerInstance = new CarrinhoControler();
export default CarrinhoControlerInstance;