import { Essencia,Carvao,Aluminio,Acessorio } from "./Classes" // import/no-anonymous-default-export
class CatalogoControler{
    TodosOsItens=[];
    ObterCategoriaEssencias=()=>{
        return this.TodosOsItens.filter(Valor => Valor instanceof Essencia && Valor.Tem)
    }

    ObterCategoriaCarvao_Aluminio=()=>{
        return this.TodosOsItens.filter(Valor => (Valor instanceof Carvao || Valor instanceof Aluminio) && Valor.Tem);
    }

    ObterCategoriaAcessorios=()=>{
        return this.TodosOsItens.filter(Valor => Valor instanceof Acessorio && Valor.Tem)
    }

    FiltrarPorPesquisa=()=>{}

    FiltrarPorFiltro=()=>{}

    AdicionarProduto=(Valor)=>{
        Valor.Id=this.TodosOsItens.length
        this.TodosOsItens.push(Valor)
    }

    AtualizarProduto=()=>{}

    ObterItemByIndex=(Index)=>{return this.TodosOsItens[Number(Index)]}

    ObterItemDaMarca=(Produto)=>{
        Produto= ((!isNaN(parseFloat(Produto)) && isFinite(Produto)) &&
            this.TodosOsItens[Produto]) || Produto

        return this.TodosOsItens.filter(Valor => Valor.Marca && Valor.Marca===Produto.Marca && 
            Valor!==Produto && 
            Valor.constructor.name===Produto.constructor.name)
    }
    ObterItemRelevantes = (Produto) => {
        Produto = ((!isNaN(parseFloat(Produto)) && isFinite(Produto)) &&
            this.TodosOsItens[Produto]) || Produto;
        const indices = [];
    
        if (Produto instanceof Essencia) {
            return this.TodosOsItens.filter(Valor => Valor instanceof Essencia &&
                Produto.Sabor.toLowerCase().split(" ").every(Sabor => Valor.Sabor.toLowerCase().split(" ").includes(Sabor)) &&
                Valor !== Produto
            )
        } else if (Produto instanceof Acessorio) {
            return this.TodosOsItens.filter(Valor => Valor instanceof Acessorio && Valor.Especificacao===Produto.Especificacao)
        }
        return indices;
    }; 
}
const CatalogoControlerInstance = new CatalogoControler();
export default CatalogoControlerInstance;